#sidebar{
    background-color: $primary;
    box-shadow: 0 10px 16px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%);
    color: white;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: space-between;
    min-width: 250px;
    overflow-y: auto;
    position: relative;
    width: 250px;

    ul li a {
        color: white;
        text-align: left;
        text-decoration: none;
        padding: 15px;
    }

    ul li a i{
        font-size: 1.8em;
    }
}

.sidebar-item{
    font-size: 20px !important;
    padding: 5px;
    transition: opacity .2s ease-in-out;
}

.current{
    font-size: x-large;
}

.nav-item{
    &:hover{
        color: black  !important;
    }
}

.sidebar-top-title{
    display: flex;
    justify-content: space-around;
    font-size: x-large;
}

/* Shrinking the sidebar to 80px and center aligining its content*/
#sidebar.active {
    min-width: 80px;
    max-width: 80px;
    text-align: center;
    
    ul li a {
        text-align: center;
    }
    
    ul li a i {
        margin-right: 0;
        display: block;
    }

    .sidebar-header h3 {
        display: none;
    }
    .sidebar-header strong {
        display: block;
    }
} 

@media (max-width: 768px) {
    /* 80px and its content aligned to centre. Pushing it off the screen with the
       negative left margin
    */


    #sidebar.active {
        min-width: 80px;
        max-width: 80px;
        text-align: center;
        margin-left: -80px !important;
    }


